@mixin white-gradient {
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0) 100%
  );
}

$animationSpeed: 40s;

// Animation
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-250px * 7));
  }
}

// Styling
.slider {
  background: white;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.1);
  height: 100px;
  margin: 5% auto;
  overflow: hidden;
  position: relative;
  top: 5%;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;

  &::before,
  &::after {
    @include white-gradient;
    content: "";
    height: 100px;
    position: absolute;
    width: 200px;
    z-index: 2;
  }

  &::after {
    right: 0;
    top: 0;
    transform: rotateZ(180deg);
  }

  &::before {
    left: 0;
    top: 0;
  }

  .slide-track {
    animation: scroll $animationSpeed linear infinite;
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(250px * 14);
  }

  .mango {
    img {
      height: 20px;
    }
  }
  .slide {
    height: 100px;
    width: 250px;
    display: grid;
    place-items: center;
    img {
      height: 30px;
      object-fit: contain;
    }
  }
}
